import { Component, OnInit, ViewChild } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { IonInfiniteScroll } from '@ionic/angular';
import { InicioService } from 'src/app/providers/Almacenamiento/inicio.service';
import { UsuariosLoginService } from 'src/app/providers/Api/UsuariosLogin.service';
import { ToastServices } from 'src/app/providers/Utilidades/ToastService.service';
import { InventariosService } from '../../../providers/Api/Inventarios.service';

@Component({
  selector: 'app-ivt-movimientos',
  templateUrl: './ivt-movimientos.page.html',
  styleUrls: ['./ivt-movimientos.page.scss'],
})
export class IvtMovimientosPage implements OnInit {

  movimientos: any = [];
  desde = 0;
  carga = false;

  @ViewChild('inifinito', {static: false}) infinite: IonInfiniteScroll;

  constructor(
    private almacenamiento: InicioService,
    private ivtApi: InventariosService,
    private toastCtrl: ToastServices,
    private router: Router,
    private activeRoute: ActivatedRoute
  ) { }

  ngOnInit() {
  }

  ionViewWillEnter() {

    this.inicio();

  }

  async inicio() {
      const login = await this.almacenamiento.obtenerToken();

      if (login) {
        this.ivtApi.leerMovmientos(login.token, {desde: this.desde, id: this.activeRoute.snapshot.paramMap.get('id')}).subscribe((data: any) => {
          if (data.exe) {
              this.movimientos= data.response;
              this.carga = true;
            } else {
              this.toastCtrl.alertApplication('No se pudo traer los movimientos, intentelo m치s tarde', false);
            }
        }, (error) => {
          console.log(error)
            this.toastCtrl.alertApplication('No se pudo traer los movimientos, intentelo m치s tarde', false);
        })
      }
  }

  refrescando( event ) {
    this.desde = 0;
    this.inicio().then(() => {
      event.detail.complete();
    })
  }

  async loadData( event ) {
    this.desde = this.desde + 10;
    console.log(this.desde)
    const info = await this.almacenamiento.obtenerToken();

    if ( info ) {

      this.ivtApi.leerMovmientos(info.token, {desde: this.desde, id: this.activeRoute.snapshot.paramMap.get('id')}).subscribe((data: any) => {
        console.log(data);
        if (data.exe) {
           
            if ( data.response.length > 0  ) {
               
            data.response.forEach((item) => {
              
              this.movimientos.push(item)
            })
            event.target.complete();
           } else {
            event.target.disabled = true;
            this.desde = 0;
           }
          } else {
            this.toastCtrl.alertApplication('No se pudo traer los movimientos, intentelo m치s tarde', false);
          }
      }, (error) => {
        console.log(error)
          this.toastCtrl.alertApplication('No se pudo traer los movimientos, intentelo m치s tarde', false);
      })

    }
  } 



}
